.review-block {
  color: $color-night-rider;

  &__list {
    display: grid;
    margin: 0 0 40px;
    padding: 0 0 13px;

    list-style: none;

    border-bottom: 2px solid $color-ghost-white;

    grid-row-gap: 12px;
  }

  .item-list {
    display: grid;
    margin-bottom: 20px;

    grid-column-gap: 20px;
    grid-template-columns: 160px auto;
  }

  &__buttons {
    position: relative;

    display: flex;
    justify-content: center;

    .up-btn {
      position: absolute;
      top: 5px;
      right: -18px;
    }
  }
}
