.product-similar {
  color: $color-night-rider;

  .title--h3 {
    margin-bottom: 40px;
  }

  &__slider {
    position: relative;

    display: flex;
    width: 100%;
    max-width: 1112px;
    margin: 0 auto;
  }

  &__slider-list {
    display: flex;
    width: calc(100% + 16px);
    max-width: 936px;
    margin: 0 auto;
  }

  .product-card {
    display: none;
    width: calc(33.3% - 16px);
    margin: 0 16px;

    &.is-active {
      display: flex;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
