.custom-checkbox {
  position: relative;

  display: flex;

  &__error {
    position: absolute;
    bottom: 0;
    left: 0;

    color: $color-flamingo;

    opacity: 0;

    transition: opacity $trans-default;
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  label {
    position: relative;

    padding-left: 30px;

    font-size: 14px;
    line-height: 20px;

    span {
      margin-bottom: 0;
      margin-left: 0;
    }
  }

  .custom-checkbox__label {
    cursor: pointer;
  }

  .custom-checkbox__icon {
    position: absolute;
    left: 0;

    width: 20px;
    height: 20px;

    border: 2px solid $color-lavender-grey;
    border-radius: 4px;
    cursor: pointer;

    transition: border-color $trans-default, background-color $trans-default, background-image $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover {
        border-color: $color-medium-slate-blue;
      }

      &:focus-visible {
        border-color: $color-governor-bay;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;

      width: 20px;
      height: 20px;

      background-color: $color-transparent;
      background-image: url("../img/svg/check.svg");
      background-repeat: no-repeat;
      background-position: center;
      border-radius: 4px;
      transform: translate(-50%, -50%);

      transition: background-color $trans-default, background-image $trans-default;
    }
  }

  label:hover {
    .custom-checkbox__icon {
      border-color: $color-medium-slate-blue;
    }
  }

  input:focus-visible + .custom-checkbox__icon {
    border-color: $color-governor-bay;
  }

  input:checked + .custom-checkbox__icon {
    &::before {
      background-color: $color-medium-slate-blue;
    }
  }

  input:disabled + .custom-checkbox__icon {
    border-color: $color-white-smoke;

    pointer-events: none;

    &::before {
      background-color: $color-white-smoke;
      background-image: none;

      pointer-events: none;
    }
  }

  input:disabled + .custom-checkbox__icon + .custom-checkbox__label {
    color: $color-gainsboro;

    pointer-events: none;
  }

  input:checked:disabled + .custom-checkbox__icon {
    border-color: $color-white-smoke;

    &::before {
      background-color: $color-white-smoke;
      background-image: url("../img/svg/check-disabled.svg");

      pointer-events: none;
    }
  }

  input:checked:disabled + .custom-checkbox__icon + .custom-checkbox__label {
    color: $color-gainsboro;

    pointer-events: none;
  }
}
