.form-search {
  position: relative;

  width: 280px;
  margin-left: auto;

  label {
    display: flex;
    align-items: center;
    min-height: 44px;

    background: $color-default-white;
    border: 2px solid transparent;
    border-radius: 8px;

    transition: box-shadow $trans-default, border $trans-default;

    &:hover {
      box-shadow: 0 4px 16px rgba(56, 56, 117, 0.2);
    }
  }

  label:focus-within {
    border-color: $color-medium-slate-blue;
  }

  .form-search__icon {
    width: 16px;
    height: 16px;
    margin-right: 9px;
    margin-left: 19px;
  }

  &__input {
    width: 100%;
    padding: 0;
    padding-right: 30px;

    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: $color-night-rider;

    border: none;
    outline: none;
  }

  ::placeholder {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    color: $color-lavender-grey;
  }

  &__select-list {
    position: absolute;
    top: 49px;
    left: 0;
    z-index: 4;

    width: 100%;
    min-height: 100%;
    max-height: 176px;
    margin: 0;
    padding: 8px 0 8px 4px;
    overflow-y: scroll;

    list-style: none;

    background: $color-default-white;
    border-radius: 8px;
    box-shadow: 0 4px 16px rgba(56, 56, 117, 0.2);

    appearance: none;
    visibility: hidden;
    opacity: 0;
    transition: opacity $trans-default;
  }

  .scroller::-webkit-scrollbar {
    width: 12px;

    border-radius: 8px;
  }

  .scroller::-webkit-scrollbar-thumb {
    background: $color-lavender-grey;
    background-clip: content-box;
    border: 4px solid $color-transparent;
    border-radius: 8px;
  }

  .scroller {
    border-radius: 8px;

    scrollbar-color: $color-lavender-grey $color-default-white;
    scrollbar-width: thin;
  }

  &__select-item {
    padding: 8px 16px;

    font-weight: 600;
    color: $color-night-rider;

    border-radius: 8px;
    cursor: pointer;

    transition: background-color $trans-default;

    &:hover {
      background-color: $color-ghost-white;
    }
  }

  &__reset {
    position: absolute;
    top: 0;
    right: 0;

    display: none;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    padding: 0;

    color: $color-medium-slate-blue;

    background: $color-transparent;
    border: none;
    cursor: pointer;

    transition: color $trans-default;

    svg {
      width: 10px;
      height: 10px;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible {
        color: $color-flamingo;
      }
    }
  }

  &.list-opened {
    form label {
      border: 2px solid $color-medium-slate-blue;
    }

    .form-search__select-list {
      visibility: visible;
      opacity: 1;
    }

    .form-search__reset {
      display: flex;
    }
  }
}
