.page-content {
  padding-bottom: 80px;

  .title--h2 {
    margin-bottom: 35px;
  }

  .title--h3 {
    margin-bottom: 24px;
  }

  &__columns {
    display: grid;
    grid-column-gap: 60px;
    grid-template-areas: "aside content"
      "aside content"
      "aside pagination";
    grid-template-columns: 236px 1fr;
    grid-template-rows: min-content 1fr min-content;
  }

  &__section {
    padding-bottom: 60px;

    &:last-of-type {
      padding-bottom: 0;
    }
  }

  &__headed {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    align-items: center;

    .title--h3 {
      margin-bottom: 0;
    }
  }
}
