.review-card {
  padding-top: 24px;

  border-top: 2px solid $color-ghost-white;

  &__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  &__data {
    font-size: 14px;
    line-height: 20px;
    color: $color-lavender-grey;
  }

  &__rate {
    margin-bottom: 20px;

    svg {
      width: 17px;
      height: 16px;
      margin-right: 5px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }
}
