.cross-btn {
  width: 40px;
  height: 40px;
  padding: 0;

  color: $color-medium-slate-blue;

  background: $color-transparent;
  border: none;
  cursor: pointer;

  transition: color $trans-default;

  svg {
    width: 10px;
    height: 10px;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus-visible {
      color: $color-flamingo;
    }

    &:active {
      color: $color-governor-bay;
    }
  }
}
