.tabs {
  max-width: 100%;
  margin-top: 30px;

  &__controls {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 25px;
  }

  &__control {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    min-width: 157px;
    padding: 8px 20px;

    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    color: $color-night-rider;

    background-color: $color-transparent;
    border: none;
    border-radius: 20px;
    cursor: pointer;

    transition: background-color $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible,
      &:active {
        background-color: $color-ghost-white;
      }
    }

    &:disabled {
      color: $color-gainsboro;

      pointer-events: none;
    }

    &.disabled {
      color: $color-gainsboro;

      pointer-events: none;
    }

    &.is-active {
      background-color: $color-lavender;

      pointer-events: none;
      outline: none;

      &:focus-visible {
        background-color: $color-lavender;
      }
    }

    &:disabled.is-active {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }

    &.disabled.is-active {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }
  }

  &__content {
    position: relative;

    margin-bottom: 40px;
    padding-top: 20px;

    border-top: 2px solid $color-ghost-white;

    transition: height 0.3s;
  }

  &__element {
    position: absolute;

    width: 100%;

    visibility: hidden;
    opacity: 0;

    transition: opacity $trans-default, visibility $trans-default;
  }

  &__element.is-active {
    z-index: 1;

    width: 100%;

    visibility: visible;
    opacity: 1;
  }
}
