.banner {
  position: relative;

  width: 100%;
  min-height: 280px;
  margin: 0 auto;

  background-color: $color-night-rider;

  img {
    position: absolute;
    top: 0;

    width: 100%;
    height: 100%;
    margin: 0 auto;

    object-fit: cover;
    object-position: right;
  }

  &__info {
    position: relative;

    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1280px;
    margin: 0 auto;
    padding: 40px 140px;
  }

  &__message {
    margin: 0;

    font-weight: 700;
    color: $color-apple;
  }

  .title--h1 {
    margin-bottom: 5px;

    color: $color-default-white;
  }

  &__text {
    margin: 0 0 30px;

    font-size: 20px;
    line-height: 24px;
    color: $color-default-white;
  }

  .btn {
    align-self: flex-start;
  }
}
