.product {
  color: $color-night-rider;

  .container {
    display: grid;

    grid-column-gap: 40px;
    grid-template-columns: 560px auto;
  }

  .title--h3 {
    margin-bottom: 8px;
  }

  &__img {
    width: 560px;
    height: 480px;

    img {
      width: 100%;
      height: 100%;

      border-radius: 8px;

      object-fit: cover;
    }
  }

  &__rate {
    margin-bottom: 18px;

    svg {
      width: 17px;
      height: 16px;
      margin-right: 5px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  &__price {
    margin: 0 0 16px;

    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
  }

  &__tabs-controls {
    max-width: 516px;
    margin-left: 24px;
  }

  &__tabs-list {
    width: 100%;
    max-width: 456px;
    margin: 0 0 0 46px;
    padding: 0;

    list-style: none;

    .item-list {
      display: grid;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 20px;

      grid-column-gap: 20px;
      grid-template-columns: 160px auto;
    }
  }

  &__tabs-text {
    p {
      margin: 0 0 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
