.custom-input,
.custom-textarea {
  position: relative;

  display: flex;

  &__label {
    display: flex;
    margin-bottom: 8px;

    svg {
      width: 9px;
      height: 9px;
      margin-top: 5px;
      margin-left: 5px;
    }
  }

  label {
    display: flex;
    flex-direction: column;
    width: 100%;

    input,
    textarea {
      flex-grow: 1;
      min-height: 40px;
      padding: 8px 20px;

      color: $color-night-rider;

      border: 2px solid $color-lavender-grey;
      border-radius: 8px;
      outline: none;

      transition: border $trans-default, box-shadow $trans-default;

      &::placeholder {
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        color: $color-lavender-grey;
      }

      &:hover {
        border: 2px solid $color-medium-slate-blue;
      }

      &:focus {
        border: 2px solid $color-medium-slate-blue;
      }

      &:disabled {
        background-color: $color-white-smoke;
        border-color: $color-white-smoke;

        &::placeholder {
          color: $color-gainsboro;
        }
      }
    }
  }

  &__error {
    position: absolute;
    top: 6px;
    right: 0;

    margin: 0;

    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    color: $color-flamingo;

    opacity: 0;

    transition: opacity $trans-default;
  }

  &__success {
    position: absolute;
    top: 6px;
    right: 0;

    margin: 0;

    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    color: $color-apple;

    opacity: 0;

    transition: opacity $trans-default;
  }

  &.is-invalid {
    input {
      border: 2px solid $color-flamingo;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-flamingo, 0.8);
      }
    }

    .custom-input__error {
      opacity: 1;
    }
  }

  &.is-valid {
    input {
      border: 2px solid $color-apple;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-apple, 0.7);
      }
    }

    .custom-input__success {
      opacity: 1;
    }
  }
}

.custom-textarea {
  label textarea {
    min-height: 120px;
  }

  &.is-invalid {
    textarea {
      border: 2px solid $color-flamingo;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-flamingo, 0.8);
      }
    }

    .custom-textarea__error {
      opacity: 1;
    }
  }

  &.is-valid {
    textarea {
      border: 2px solid $color-medium-slate-blue;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-medium-slate-blue, 0.7);
      }
    }
  }
}
