.basket-form {
  display: flex;
  justify-content: space-between;
  width: 100%;

  .btn {
    margin-top: 20px;
  }

  form {
    display: flex;
    align-items: center;
    width: 100%;

    color: $color-night-rider;

    .custom-input {
      width: 308px;
      margin-right: 10px;

      &__error {
        top: 3px;
      }

      &__sucess {
        top: 3px;
      }
    }

    label {
      margin-bottom: 8px;

      line-height: 20px;
    }

    input {
      padding: 8px 20px;

      background-color: $color-default-white;
      border: 2px solid $color-lavender-grey;
      border-radius: 8px;
      outline: $color-medium-slate-blue;

      &::placeholder {
        font-weight: 600;
        line-height: 24px;
        color: $color-lavender-grey;
      }

      &:focus {
        border-color: $color-medium-slate-blue;
      }
    }
  }
}
