.form-checkbox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;

  & label {
    display: flex;
    align-items: center;
    overflow: hidden;

    font-size: 14px;
    line-height: 20px;

    cursor: pointer;

    &::before {
      content: "";

      display: block;
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      margin-right: 10px;

      border: 2px solid $color-lavender-grey;
      border-radius: 4px;
      cursor: pointer;

      transition: border-color $trans-default;
    }
  }

  & input {
    &:hover + label::before {
      border-color: $color-medium-slate-blue;
    }

    &:focus-visible + label::before {
      border-color: $color-medium-slate-blue;
    }

    &:checked + label::before {
      background-color: $color-medium-slate-blue;
      background-image: url("../img/svg/check.svg");
      background-repeat: no-repeat;
      background-position: center;
      border-color: $color-transparent;
    }

    &:disabled + label {
      color: $color-gainsboro;

      pointer-events: none;
    }

    &:disabled + label::before {
      background: $color-white-smoke;
      border-color: $color-transparent;

      pointer-events: none;
    }

    &:disabled:hover + label {
      pointer-events: none;
    }
  }
}
