.breadcrumbs {
  padding-top: 32px;
  padding-bottom: 20px;

  &__list {
    display: flex;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    position: relative;

    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    font-size: 14px;
    line-height: 20px;
    color: $color-medium-slate-blue;

    transition: color $trans-default;

    svg {
      width: 5px;
      height: 8px;
      margin-left: 20px;
    }

    &:active {
      color: $color-night-rider;
    }

    &--active {
      color: $color-night-rider;

      pointer-events: none;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus-visible {
          color: $color-night-rider;
        }
      }
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible {
        color: $color-perano;
      }
    }
  }
}
