.header {
  background-color: $color-ghost-white;

  .container {
    display: flex;
    align-items: center;
  }

  &__logo {
    padding-top: 4px;

    svg {
      width: 100px;
      height: 36px;
    }
  }

  &__main-nav {
    margin-left: 40px;
  }

  &__basket-link {
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    margin-left: 40px;

    color: $color-medium-slate-blue;

    background: $color-default-white;
    border-radius: 8px;

    transition: box-shadow $trans-default;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible {
        box-shadow: 0 4px 16px rgba(56, 56, 117, 0.2);
      }
    }

    svg {
      width: 16px;
      height: 16px;
    }
  }

  &__basket-count {
    position: absolute;
    top: 8px;
    right: 7px;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 14px;
    height: 14px;

    font-weight: 700;
    font-size: 10px;
    line-height: 14px;
    color: $color-default-white;

    background-color: $color-flamingo;
    border-radius: 50%;
  }
}
