.footer {
  padding: 40px 0 60px;

  background-color: $color-governor-bay;

  .container {
    display: flex;
  }

  &__logo {
    display: inline-block;

    svg {
      width: 100px;
      height: 36px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    max-width: 153px;
    margin-right: 143px;
  }

  &__description {
    margin: 7px 0 0;

    font-size: 14px;
    line-height: 20px;
    color: $color-blue-bell;
  }

  &__nav {
    display: grid;
    margin: 0;
    padding: 0;

    list-style: none;
    column-gap: 162px;

    grid-template-columns: repeat(3, 150px);
    grid-row-gap: 40px;
  }

  &__nav-item {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0 0 20px;

    font-weight: 700;
    color: $color-default-white;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
