.form-review {
  display: flex;
  flex-direction: column;
  min-width: 400px;

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__wrapper {
    display: flex;
    margin-bottom: 5px;
    padding-top: 23px;
  }

  &__label {
    display: inline-block;
    align-self: flex-start;
    margin-bottom: 5px;

    font-size: 16px;
    line-height: 20px;
    color: $color-night-rider;

    &--required {
      position: relative;

      &::before {
        content: "*";
        position: absolute;
        top: 2px;
        right: -6px;

        width: 5px;
        height: 5px;

        font-size: 12px;
        line-height: 16px;
        color: $color-flamingo;
      }
    }
  }

  &__input {
    width: 100%;
    padding: 8px 20px;

    font-weight: 600;
    font-size: 16px;
    line-height: 24px;

    background-color: inherit;
    border: 2px solid $color-lavender-grey;
    border-radius: 8px;

    &--textarea {
      height: 70px;
    }

    &::placeholder {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: $color-lavender-grey;
    }
  }

  &__warning {
    display: block;
    margin: 0;
    padding-right: 5px;

    font-size: 14px;
    line-height: 18px;
    text-align: end;
    color: $color-flamingo;
  }

  &__button {
    margin-top: 7px;
    margin-right: 70px;
    margin-left: 70px;
  }

  &__rate {
    margin-bottom: 33px;
  }

  &__btn {
    width: 100%;
  }
}
