.title {
  margin: 0;

  font-weight: 700;
  color: inherit;

  &--h1 {
    font-size: 48px;
    line-height: 64px;
  }

  &--h2 {
    font-size: 40px;
    line-height: 64px;
  }

  &--h3 {
    font-size: 32px;
    line-height: 48px;
  }

  &--h4 {
    font-size: 20px;
    line-height: 32px;
  }

  &--h5 {
    font-size: 16px;
    line-height: 20px;
  }
}
