.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: rgba($color-default-black, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px;
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;

    padding: 32px 40px 36px;

    color: $color-night-rider;

    background-color: $color-default-white;
    border-radius: 8px;
    box-shadow: $shadow;
  }

  .cross-btn {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }

  .title--h4 {
    margin-bottom: 24px;

    text-align: center;
  }

  &__icon {
    display: block;
    width: 86px;
    height: 80px;
    margin: 0 auto 24px;
  }

  &__info {
    display: flex;
    margin-bottom: 24px;
  }

  &__img {
    width: 140px;
    height: 120px;

    img {
      width: 100%;
      height: 100%;

      border-radius: 8px;

      object-fit: cover;
    }
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -10px;

    margin-top: -16px;

    text-align: center;
  }

  &__btn {
    margin-top: 16px;
    margin-left: 10px;


    &--fit-width {
      width: 100%;
    }

    &--half-width {
      width: calc((100% - 10px * 2) / 2);
    }
  }

  &.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;

    pointer-events: auto;
  }

  &--narrow {
    .modal__content {
      width: 320px;
    }
  }
}
