.rate {
  position: relative;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;

  color: $color-lavender2;

  border: none;

  transition: color $trans-default;


  &.is-invalid {
    .rate__message {
      opacity: 1;
    }
  }

  &__caption {
    display: flex;
    margin-bottom: 5px;

    font-size: 16px;
    line-height: 20px;
    color: $color-night-rider;

    svg {
      width: 9px;
      height: 9px;
      margin-top: 2px;
      margin-left: 5px;
    }
  }

  &__bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  &__progress {
    font-size: 24px;
    line-height: 32px;
    color: $color-lavender-grey;
  }

  &__group {
    position: relative;

    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    width: 320px;
    height: 48px;

    transition: background-image $trans-default;


    label {
      margin-right: 15px;

      &:first-of-type {
        margin-right: 0;
      }
    }
  }

  &__count {
    margin: 0;
    margin-left: 12px;

    color: $color-lavender-grey;
  }

  &__message {
    position: absolute;
    top: -26px;
    right: 0;

    margin: 0;

    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    color: $color-flamingo;

    opacity: 0;
  }
}

.rate__group:not(:checked) > label {
  float: right;
  width: 51px;
  height: 48px;
  overflow: hidden;

  font-size: 30px;
  color: $color-default-white;
  white-space: nowrap;

  cursor: pointer;
  background: url("../img/svg/star-default.svg") no-repeat top 0 left 0;
  background-size: 51px 48px;
}

.rate__group input:checked ~ label {
  background: url("../img/sprite/icon-full-star.svg") no-repeat top 0 left 0;
  background-size: 51px 48px;
}

.rate__group input:checked:disabled ~ label {
  background: url("../img/svg/star-disabled.svg") no-repeat top 0 left 0;
  background-size: 51px 48px;

  pointer-events: none;
}

.rate__group input:disabled ~ label {
  background: url("../img/svg/star-disabled2.svg") no-repeat top 0 left 0;
  background-size: 51px 48px;

  pointer-events: none;
}

.rate__group:not(:checked) > label:hover,
.rate__group:not(:checked) > label:hover ~ label {
  background: url("../img/svg/star-hover.svg") no-repeat top 0 left 0;
  background-size: 51px 48px;
}

.rate__group > input:focus-visible + label {
  outline: 1px solid $color-flamingo;
}
