.catalog-sort {
  display: flex;
  align-items: center;
  width: 100%;

  font-size: 16px;
  line-height: 20px;

  form {
    width: 100%;
  }

  &__inner {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .title--h5 {
    margin-right: 20px;
  }

  &__type {
    display: flex;
    flex-wrap: wrap;
  }

  &__order {
    display: flex;
    margin-left: auto;
  }

  &__btn-text {
    display: flex;
    align-items: center;

    & input[type="radio"] {
      appearance: none;
    }

    label {
      padding: 8px 22px;

      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      color: $color-medium-slate-blue;

      background-color: $color-ghost-white;
      border-radius: 20px;
      cursor: pointer;

      transition: color $trans-default, background-color $trans-default;

      user-select: none;

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus-visible {
          background-color: $color-lavender;
        }
      }
    }

    &:not(:last-child) {
      margin-right: 7px;
    }

    /* Checked */
    & input[type="radio"]:checked + label {
      color: $color-default-white;

      background-color: $color-medium-slate-blue;

      pointer-events: none;
    }

    /* Disabled */
    & input[type="radio"]:disabled + label {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }

    /* Checked + Disabled */
    & input[type="radio"]:checked:disabled + label {
      color: $color-white-smoke;

      background-color: $color-gainsboro;
    }

    & input[type="radio"]:focus-visible + label {
      color: $color-medium-slate-blue;

      background-color: $color-lavender;
    }
  }

  &__btn {
    &--up {
      svg {
        transform: scale(-1, 1);
      }
    }

    &--down {
      margin-left: 10px;
    }

    label {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 36px;
      height: 36px;
      padding: 0 size(12px);

      font-weight: 600;
      color: $color-medium-slate-blue;

      background-color: $color-ghost-white;
      border-radius: 50%;
      cursor: pointer;

      transition: background-color $trans-default;

      user-select: none;

      svg {
        width: 16px;
        height: 14px;
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus-visible {
          background-color: $color-lavender;
        }
      }
    }

    & input[type="radio"] {
      appearance: none;
    }

    /* Checked */
    & input[type="radio"]:checked + label {
      color: $color-default-white;

      background-color: $color-medium-slate-blue;

      pointer-events: none;
    }

    /* Disabled */
    & input[type="radio"]:disabled + label {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }

    /* Checked + Disabled */
    & input[type="radio"]:checked:disabled + label {
      color: $color-white-smoke;

      background-color: $color-gainsboro;

      pointer-events: none;
    }

    & input[type="radio"]:focus-visible + label {
      color: $color-medium-slate-blue;

      background-color: $color-lavender;
    }
  }
}
