.catalog-filter {
  width: 100%;

  .title--h5 {
    float: left;
    margin-bottom: 16px;
  }
}

.catalog-filter__block {
  position: relative;

  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0;
  padding: 16px 0;

  border: none;

  &:first-of-type {
    padding-top: 8px;
  }
}

.catalog-filter__item {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }
}

.catalog-filter__price-range {
  position: relative;

  display: flex;
  justify-content: space-between;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;

    width: 10px;
    height: 2px;
    margin-top: -2px;

    background-color: $color-governor-bay;
    transform: translateX(-50%);
  }
}

.catalog-filter__price-range input {
  width: 108px;
  height: 40px;
  padding: 0 20px;

  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: $color-night-rider;

  border: 2px solid $color-lavender-grey;
  border-radius: 8px;
  outline: $color-lavender-grey;

  transition: border-color $trans-default;

  &::placeholder {
    color: $color-lavender-grey;
  }

  &:disabled {
    background-color: $color-white-smoke;
    border-color: $color-transparent;

    &::placeholder {
      color: $color-gainsboro;
    }
  }
}

.catalog-filter__reset-btn {
  width: 100%;
  margin-top: 24px;
}
