.basket-item {
  position: relative;

  display: grid;
  padding-bottom: 32px;

  color: $color-night-rider;

  border-bottom: 2px solid $color-ghost-white;

  grid-column-gap: 40px;
  grid-template-columns: 140px 401px 140px 138px 140px 40px;

  &:last-child {
    padding-bottom: 0;

    border-bottom: none;
  }

  &__img {
    width: 140px;
    height: 120px;

    img {
      width: 100%;
      height: 100%;

      border-radius: 8px;

      object-fit: cover;
    }
  }

  &__title {
    margin: 0 0 10px;

    font-size: 20px;
    line-height: 32px;
  }

  &__list {
    margin: 0;
    padding: 0;

    list-style: none;
    color: $color-lavender-grey;
  }

  &__price,
  &__total-price {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
  }

  &__price {
    margin: 0;
  }

  .cross-btn {
    margin-top: 40px;
  }

  &--short {
    display: flex;
    grid-column-gap: 0;
    padding-bottom: 20px;
    border-bottom: none;

    .basket-item__description {
      margin-right: 34px;
      margin-left: 34px;
    }

    .basket-item__title {
      margin: 0 0 8px;

      font-size: 16px;
      line-height: 24px;
    }

    .basket-item__list {
      margin: 0 0 8px;
      padding: 0;

      list-style: none;
      font-size: 14px;
      line-height: 20px;
      color: $color-blue-bell;
    }

    .basket-item__price {
      margin: 0;

      font-weight: 700;
      font-size: 20px;
      line-height: 32px;
      justify-content: flex-start;
    }
  }
}
