.slider-controls {
  position: absolute;
  top: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0;

  color: $color-medium-slate-blue;

  background-color: $color-ghost-white;
  border: none;
  border-radius: 50%;
  transform: translateY(-50%);
  cursor: pointer;

  transition: opacity $trans-default;

  svg {
    width: 7px;
    height: 12px;
  }

  &:disabled {
    color: $color-gainsboro;

    background-color: $color-white-smoke;

    pointer-events: none;
  }

  &.disabled {
    color: $color-gainsboro;

    background-color: $color-white-smoke;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus-visible {
      background-color: $color-lavender;
    }

    &:active {
      color: $color-medium-slate-blue;

      background-color: $color-perano;

      pointer-events: none;
    }
  }

  &--prev {
    left: 0;

    margin-right: 16px;
  }

  &--prev svg {
    transform: rotate(180deg);
  }

  &--next {
    right: 0;
  }
}
