.product-card {
  display: flex;
  flex-direction: column;

  font-size: 16px;
  line-height: 24px;
  color: $color-night-rider;

  background-color: $color-default-white;

  &__img {
    position: relative;

    padding-bottom: calc((240 / 280) * 100%);

    img {
      position: absolute;
      top: 0;
      left: 0;

      width: 100%;
      height: 100%;

      border-radius: 8px;

      object-fit: cover;
    }
  }

  &__rate {
    width: 100%;

    svg {
      width: 17px;
      height: 16px;
      margin-right: 5px;

      &:last-of-type {
        margin-right: 0;
      }
    }
  }

  &__info {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 10px 0 5px;
  }

  &__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: auto;
  }

  &__btn {
    min-width: 150px;

    svg {
      margin-right: 8px;
    }

    &:disabled {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }

    &.disabled {
      color: $color-gainsboro;

      background-color: $color-white-smoke;

      pointer-events: none;
    }

    &--in-cart {
      background-color: $color-transparent;
    }
  }

  &__title {
    margin: 0;
    padding-top: 8px;
  }

  &__price {
    margin: 0;
    padding-top: 4px;

    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
  }
}
