.main-nav {
  display: flex;

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__link {
    display: block;
    padding: 22px 20px;

    font-weight: 600;
    color: $color-medium-slate-blue;

    cursor: pointer;

    transition: background-color $trans-default;

    &--active {
      color: $color-medium-slate-blue;

      background-color: $color-perano;
    }

    &:active {
      color: $color-medium-slate-blue;

      background-color: $color-perano;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible {
        background-color: $color-lavender;
      }
    }
  }

  &__toggle {
    display: none;
  }
}
