.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 8px 19px;

  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: $color-medium-slate-blue;

  background-color: $color-ghost-white;
  border: 2px solid $color-transparent;
  border-radius: 8px;
  cursor: pointer;

  transition: background-color $trans-default;

  svg {
    width: 24px;
    height: 16px;
    margin-right: 8px;
  }

  &:disabled {
    color: $color-gainsboro;

    background-color: $color-white-smoke;

    pointer-events: none;
  }

  &.disabled {
    color: $color-gainsboro;

    background-color: $color-white-smoke;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus-visible  {
      background-color: $color-lavender;
    }

    &:active {
      background-color: $color-perano;
    }
  }

  &--purple {
    color: $color-default-white;

    background-color: $color-medium-slate-blue;

    &:disabled {
      color: $color-default-white;

      background-color: $color-gainsboro;
    }

    &.disabled {
      color: $color-default-white;

      background-color: $color-gainsboro;
    }

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus-visible {
        background-color: $color-perano;
      }

      &:active {
        background-color: $color-governor-bay;
      }
    }
  }

  &--purple-border {
    border-color: $color-medium-slate-blue;

    &:disabled {
      color: $color-gainsboro;

      background-color: $color-transparent;
      border-color: $color-gainsboro;

      pointer-events: none;
    }

    &.disabled {
      color: $color-gainsboro;

      background-color: $color-transparent;
      border-color: $color-gainsboro;

      pointer-events: none;
    }
  }

  &--transparent {
    background-color: $color-transparent;

    &:disabled {
      color: $color-gainsboro;

      background-color: $color-transparent;

      pointer-events: none;
    }

    &.disabled {
      color: $color-gainsboro;

      background-color: $color-transparent;

      pointer-events: none;
    }
  }
}
