.basket {
  color: $color-night-rider;

  &__list {
    display: grid;
    margin: 0 0 60px;
    padding: 0;

    list-style: none;

    grid-row-gap: 32px;
  }

  &__summary {
    display: flex;
    justify-content: space-between;
    padding: 60px 180px;

    background-color: $color-ghost-white;
    border-radius: 8px;
  }

  &__summary-order {
    width: 100%;
    max-width: 300px;

    .btn {
      width: 100%;
      margin-top: 32px;
    }
  }

  &__summary-item {
    display: flex;
    justify-content: space-between;
    margin: 0 0 16px;

    font-size: 20px;
    line-height: 32px;
    color: $color-night-rider;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  &__summary-text--total {
    font-weight: 700;
  }

  &__summary-value--bonus {
    color: $color-flamingo;
  }

  &__promo {
    width: 100%;
    max-width: 440px;

    .title--h4 {
      margin-bottom: 13px;
    }
  }
}
